#preloader {
  width: 100%;
  height: 100%;
  background: $bg-color;
  position: absolute;
  display: grid;
  align-items: center;
  justify-content: center;
}

.loading__text {
  letter-spacing: 30px;
  color: $white-color;
  font-weight: 700;
  position: relative;
  top: -50px;
  pointer-events: none;
  user-select: none;
  text-align: center;
}

.pace {
  pointer-events: none;
  user-select: none;
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 400px;
  border: 0;
  height: 1px;
  overflow: hidden;
  background: #1b1b1b;
}

.pace .pace-progress {
  max-width: 400px;
  position: absolute;
  z-index: 2000;
  top: 0;
  right: 100%;
  height: 100%;
  width: 100%;
  background: $main-color;
}
